body {
  background: darkolivegreen;
  font-family: monospace;
}

table {
  width: 100%;
  border-width: 1px;
  border-style: solid;
  border-color: black;
}

td {
  border-width: 1px;
  border-style: solid;
  border-color: black;
  background: olive;
  text-align: center;
}

tr td:first-child {
  width: 50%;
  text-align: right;
  padding-right: 10px;
}

tr.meal-title td {
  text-align: left;
  font-size: 1.2em;
  padding: 10px;
  border: none;
  background: none;
}

tr.meal-total td {
  background: goldenrod;
}
tr.meal-total td:first-child {
  background: none;
  border: none;
}
tr.spacer td {
  height: 30px;
  background: none;
  border: none;
}
tr.total td {
  background: blueviolet;
  font-size: 1.3em;
  font-weight: 900;
}
tr.total td:first-child {
  background: none;
  border: none;
}

tr.target td {
  background: lightblue;
  font-size: 1.2em;
  font-weight: 900;
}
tr.target td:first-child {
  background: none;
  border: none;
}

.diagnostic {
  background: darkslategrey;
  padding: 10px;
  max-width: 500px;
  margin: 20px auto;
  color: white;
  text-align: center;
  font-size: 1.3em;
}

ul li {
  list-style-type: none;
}

.shopping-list {
  background: #353;
  padding: 10px;
  color: #bbb;
  margin: 20px auto;
  text-align: center;
  font-size: 1em;
}
